.header {
  width: 100vw;
  height: 64px;
  display: flex;
  justify-content: space-between;
  box-shadow: 0 1px 0 0 $light-gray;

  @media (pointer: fine) {
    width: calc(100vw - 17px);
  }

  &__part {
    display: flex;

    &--left {
      gap: 64px;
    }
  }

  &__logo {
    margin-top: 20px;
    margin-left: 24px;
  }
}
